@import url("https://fonts.googleapis.com/css2?family=Raleway:wght@300&display=swap");

html,
body,
#root {
  height: 100%;
  margin: 0; /* Reset default margin */
  padding: 0; /* Reset default padding */
  background-color: #6a706d;
  color: white;
}

.first {
  background-image: url("bg.jpg");
  filter: blur(3px);
  -webkit-filter: blur(3px);
}

.App {
  display: flex;
  flex-direction: column;
  font-family: "Raleway", sans-serif;
  min-height: 100vh;
}

.App-header {
  position: absolute;
  top: 0;
  width: 100%;
  z-index: 99;
}

.inner-header {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  font-size: calc(10px + 2vmin);
  color: white;
  padding-left: 25px;
  padding-right: 25px;
}

.App-link {
  color: #61dafb;
}

.App-body {
  display: flex;
  height: 100vh;
  align-items: center;
  justify-content: center;
}

.about-container {
	position: absolute;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);
	text-align: center;
}


.Main-text {
  
  font-size: calc(20px + 4vmin);
  color: white;
  letter-spacing: 5px;
  /*width: max-content;*/
  margin: 0;
}

h4{
	letter-spacing: 5px;
}

.smaller {
  scale: 0.85;
}

.clean{
	text-decoration: none;
	color: inherit;
}

.clickable{
	cursor: pointer;
}


.hamburger-icon {
  position: relative;
  z-index: 10;
}

.hamburger-icon span {
  height: 4px;
  width: 30px;
  border-radius: 25px;
  background-color: white;
  display: block;
  margin: 5px 0px 5px 0px;
  transition: 0.7s ease-in-out;
  transform: none;
}

#openmenu:checked ~ .menu-pane {
  left: -5vw;
  transform: translateX(-5vw);
}

#openmenu:checked ~ .body-text {
display: none;
}

#openmenu:checked ~ .hamburger-icon span:nth-of-type(2) {
  transform: translate(0%, 175%) rotate(-45deg);
  background-color: white;
}

#openmenu:checked ~ .hamburger-icon span:nth-of-type(3) {
  transform: rotate(45deg);
  background-color: white;
}

#openmenu:checked ~ .hamburger-icon span:nth-of-type(1) {
  opacity: 0;
}

#openmenu:checked ~ .hamburger-icon span:nth-of-type(4) {
  opacity: 0;
}

div.menu-pane {
  position: absolute;
  transform: translateX(-105vw);
  transform-origin: (0, 0);
  width: 100vw;
  height: 100%;
  transition: 0.6s ease-in-out;
  z-index: 9;
}

.menu-pane p {
  color: black;
  font-size: 0.6em;
}

.menu-pane nav {
  padding: 10%;
  background-color: rgba(57, 66, 63, .7);
}

.menu-links li, a, span {
      transition: 0.5s ease-in-out;
}

.menu-pane ul {
  padding: 10%;
  display: inline-block;
}

.menu-pane li {
  padding-top: 20px;
  padding-bottom: 20px;
  margin-left: 10px;
    font-size: 1em;
}


.menu-pane li:first-child {
  font-size: 1.3em;
  margin-left: -10px;
}


.menu-links li a {
  color: white;
  text-decoration: none;
}


.menu-links li:hover a {
  color: #FFAB91;
}

.menu-links li:first-child:hover a {
  color: black;  
  background-color: #FFAB91;
}

#QC-info {
  background-color: #FFAB91;
    border: 2px solid;
  border-color: #FFAB91;
display: block;
  opacity: 0;
  
}

.menu-links li:first-child:hover #QC-info {
opacity: 1;
}

.menu-links li:first-child:hover #DC-info {
opacity: 1;
}

#DC-info {
  background-color: #FFAB91;
    border: 2px solid;
  border-color: #FFAB91;
display: block;
  opacity: 0;
}


.menu-links li:first-child a {
  padding: 5px;
}

input.hamburger-checkbox {
  position: absolute;
  z-index: 11;
  width: 30px;
  opacity: 0;
  height: 30px;
  cursor: pointer;
}
